/* eslint-disable */
<template>
  <form class="card auth-card" @submit.prevent="submitHandler">
    <div class="card-content">
      <span class="card-title">Обмен услугами</span>
      <div class="input-field">
        <input
            id="email"
            type="text"
            v-model.trim="email"
            required
        >
        <label for="email">Email</label>
        
      </div>
      <div class="input-field">
        <input
            id="phone"
            type="tel"
            v-model.trim="phone"
            required
        >
        <label for="phone">Номер телефона</label>
        
      </div>
      <div class="input-field">
        <input
            id="password"
            type="password"
            v-model.trim="password"
            required
        >
        <label for="password">Пароль</label>
        
      </div>
      <div class="input-field">
        <input
            id="name"
            type="text"
            v-model.trim="name"
            required
        >
        <label for="name">Имя</label>
        
      </div>
      <p>
        <label>
          <input type="checkbox" v-model="agree" required/>
          <span>С правилами согласен</span>
        </label>
      </p>
    </div>
    <div class="card-action">
      <div>
        <button
            class="btn waves-effect waves-light auth-submit"
            type="submit"
        >
          Зарегистрироваться
          <i class="material-icons right">send</i>
        </button>
      </div>

      <p class="center">
        Уже есть аккаунт?
        <router-link to="/LoginView"
            custom
            v-slot="{href, navigate}"
            >
        <a :href="href" @click="navigate">Войти!</a>
        </router-link>
      </p>
    </div>
  </form>
</template>

<script>
import axios from 'axios';
import Cookies from 'js-cookie'

export default {

  name: 'RegisterView',
  data: () => ({
    email: '',
    password: '',
    name: '',
    phone: '',
    token: '',
    agree: false
  }),
  
  methods: {

    async submitHandler() {
      

      const formData = {
        email: this.email,
        password: this.password,
        name: this.name,
        phone: this.phone,
        agree: true

      }

      const config = {
          headers: {
            'Content-Type': 'application/json',
          },
        };

        axios.post('https://localhost:5050/auth/registration', formData, config)
          .then(response => {
            this.token = response.data.token
            // Cookies.set(this.token, token, { expires: 7 })
            this.$router.push('/HomeView')
          })
          .catch(error => {
            console.log(error);
          });
    }
  }
}
</script>